.Su {
  padding: 20px;
      margin: 0 auto;

}





.Image {
  max-width: 20rem;
  height: 10rem;
  border-radius: 5px;
  margin: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}




.cardb{
  display: flex;
  justify-content: space-around;
 
  align-items: center;
  flex-wrap: wrap;

  
}

.Heading{
  display: flex;
  justify-content:center ;
  align-items: center;
  font-family:"Poppins";
  }

.card {

  height: 35rem;
  width: 18rem; margin-top:0.5rem;
  border: 1px solid black;
  border-radius: 20px;
  display: block;
  position: relative; /* Make the container relative for absolute positioning */
  
}






.upper{
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  height:15rem;
  width:15rem;
  transform: scale(0.9);
  opacity: 1;
  transition: transform 0.3s ease-in-out;
}


.upper:hover{
  transform: scale(1);
  opacity: 1;
}

.middle{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-top: 1rem;
  left:16%;
  place-items: center;
  border-radius: 20px;
  height: 1.5rem;
  width:12rem;
  border:1px solid black;
  background:#FCC50B;
  color:black;
  font-family: "Montserrat";
  font-weight: bolder;
  font-size:0.6rem;
  z-index: 5;
}

.lower{
position: relative;
padding:0.5rem;
height:6rem;
  width:17rem;
  margin-top: -1rem;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}


.clm{
  display:flex;
  gap:1rem;
  justify-content: center;
  align-items: center;
}

.info{
  font-weight: 600;
  font-family: Poppins;
  font-size: 0.7rem;
  color: rgb(32, 0, 0);
  text-align: center;
}

.io{
  color:rgb(0, 0, 25);
  font-weight: bold;
  font-family: "Ubuntu";
}


.Title{
  text-align: center;
}

